class Context {
    constructor() {
        this.handlers = [];
    }
    addHandlers(selectors) {
        for (const selector of Object.keys(selectors)) {
            this.addHandler(selector, selectors[selector]);
        }
    }
    addHandler(selector, handler) {
        this.handlers.push({ selector, handler });
    }
    init(parent) {
        if (!parent.querySelectorAll) {
            throw new Error('Kan geen context initializeren op dit element: ' + parent);
        }
        for (const { selector, handler } of this.handlers) {
            if (selector === '') {
                handler(parent);
            }
            else {
                for (const el of Array.from(parent.querySelectorAll(selector))) {
                    handler(el);
                }
            }
        }
    }
}
window._stek_context = new Context();
export default window._stek_context;
export const init = (parent) => window._stek_context.init(parent);
